/* ---------------- 1.  ---------------- */
const COLOR_POOL = [
  '#E8F5FF', '#FFF0E8', '#F0FFE8', '#F8E8FF', '#E8FFFF', '#FFE8F0', '#FFFFE8',
];

const STENCIL_WIDTH = 140;
const STENCIL_STICKY_SIZE = { width: 80, height: 80 };
const CANVAS_STICKY_SIZE = { width: 160, height: 120 };
const VALIDATION_PADDING = 20;
const DEBOUNCE_DELAY = 16;
const LONG_PRESS_DURATION = 500;
const TAP_THRESHOLD = 200;
const MOVE_THRESHOLD = 10;

/* ---------------- 2. Toast  ---------------- */
function showToast(message, duration = 1500) {
  const toast = document.createElement('div');
  toast.textContent = message;
  Object.assign(toast.style, {
    position: 'fixed',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    padding: '12px 24px',
    background: 'rgba(0, 0, 0, 0.8)',
    color: 'white',
    borderRadius: '8px',
    fontSize: '14px',
    zIndex: '10001',
    transition: 'opacity 0.3s',
    pointerEvents: 'none',
  });

  document.body.appendChild(toast);

  setTimeout(() => {
    toast.style.opacity = '0';
    setTimeout(() => {
      if (document.body.contains(toast)) {
        document.body.removeChild(toast);
      }
    }, 300);
  }, duration);
}

