/* ---------------- 5.  ---------------- */
function debounce(func, wait) {
  let timeout;
  return function executedFunction(...args) {
    const later = () => {
      clearTimeout(timeout);
      func(...args);
    };
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
  };
}

function calcSize(label) {
  const col = 7;
  const lines = [];
  for (let i = 0; i < label.length; i += col) {
    lines.push(label.slice(i, i + col));
  }
  const newLabel = lines.join('\n');
  const baseW = 160;
  const baseH = 40;
  const stepW = 20;
  const stepH = 10;
  return {
    label: newLabel,
    width: Math.max(baseW, baseW + (Math.max(...lines.map((l) => l.length)) - 4) * stepW),
    height: baseH + (lines.length - 1) * stepH,
  };
}

function calcStickySize(text) {
  const lines = text.split('\n');
  const maxLineLength = Math.max(...lines.map((line) => line.length));
  const lineCount = lines.length;
  const charWidth = 8;
  const lineHeight = 18;
  const padding = 20;
  const calculatedWidth = Math.max(CANVAS_STICKY_SIZE.width, maxLineLength * charWidth + padding);
  const calculatedHeight = Math.max(CANVAS_STICKY_SIZE.height, lineCount * lineHeight + padding);
  return { width: calculatedWidth, height: calculatedHeight };
}

let colorIndex = 0;

function convertToHierarchyData(nodes) {
  if (!nodes || nodes.length === 0) return null;
  colorIndex = 0;
  return buildNode(nodes[0]);
}

function buildNode(node) {
  const { label: newLabel, width, height } = calcSize(node.label);
  const color = COLOR_POOL[colorIndex % COLOR_POOL.length];
  colorIndex += 1;

  const result = {
    id: node.id,
    type: 'topic',
    label: newLabel,
    width,
    height,
    color,
  };

  if (node.child && node.child.length > 0) {
    result.children = node.child.map((childNode) => buildNode(childNode));
  }

  return result;
}

function isRectOverlap(rect1, rect2, padding = 10) {
  return (
    rect1.x < rect2.x + rect2.width + padding &&
    rect1.x + rect1.width + padding > rect2.x &&
    rect1.y < rect2.y + rect2.height + padding &&
    rect1.y + rect1.height + padding > rect2.y
  );
}

/* ---------------- 6.  ---------------- */
class NodeValidator {
  constructor(graph) {
    this.graph = graph;
    this.cachedMindMapBounds = [];
  }

  cacheMindMapBounds() {
    this.cachedMindMapBounds = this.graph
      .getCells()
      .filter((c) => c.isNode() && (c.shape === 'topic' || c.shape === 'topic-child'))
      .map((node) => {
        const bbox = node.getBBox();
        return { x: bbox.x, y: bbox.y, width: bbox.width, height: bbox.height };
      });
  }

  checkOverlap(stickyBounds) {
    for (const bounds of this.cachedMindMapBounds) {
      if (isRectOverlap(stickyBounds, bounds)) {
        return false;
      }
    }
    return true;
  }

  checkInViewport(stickyBounds) {
    const graph = this.graph;
    const container = graph.container;
    const containerRect = container.getBoundingClientRect();
    const scale = graph.zoom();
    const translate = graph.translate();

    const screenX = stickyBounds.x * scale + translate.tx;
    const screenY = stickyBounds.y * scale + translate.ty;
    const screenWidth = stickyBounds.width * scale;
    const screenHeight = stickyBounds.height * scale;

    return (
      screenX >= VALIDATION_PADDING &&
      screenY >= VALIDATION_PADDING &&
      screenX + screenWidth <= containerRect.width - VALIDATION_PADDING &&
      screenY + screenHeight <= containerRect.height - VALIDATION_PADDING
    );
  }

  validate(cell, position = null) {
    if (cell.shape !== 'sticky-note') {
      return { valid: true };
    }

    const bbox = cell.getBBox();
    const stickyBounds = position
      ? { x: position.x, y: position.y, width: bbox.width, height: bbox.height }
      : { x: bbox.x, y: bbox.y, width: bbox.width, height: bbox.height };

    if (!this.checkOverlap(stickyBounds)) {
      return { valid: false, reason: 'overlap' };
    }

    if (!this.checkInViewport(stickyBounds)) {
      return { valid: false, reason: 'outOfBounds' };
    }

    return { valid: true };
  }

  validatePosition(position, size) {
    const stickyBounds = { x: position.x, y: position.y, width: size.width, height: size.height };

    if (!this.checkOverlap(stickyBounds)) {
      return { valid: false, reason: 'overlap' };
    }

    if (!this.checkInViewport(stickyBounds)) {
      return { valid: false, reason: 'outOfBounds' };
    }

    return { valid: true };
  }
}

/* ---------------- 7.  ---------------- */
class DragManager {
  constructor(graph, validator) {
    this.graph = graph;
    this.validator = validator;
    this.isDragging = false;
    this.dragStartPosition = null;
    this.currentCell = null;
  }

  startDrag(cell) {
    if (cell.shape !== 'sticky-note') return;

    this.isDragging = true;
    this.currentCell = cell;
    this.dragStartPosition = { ...cell.getPosition() };
    this.validator.cacheMindMapBounds();
    cell.attr('body/opacity', 0.8);
    cell.toFront();
  }

  endDrag(cell) {
    if (cell !== this.currentCell || !this.isDragging) return;

    this.isDragging = false;
    cell.attr('body/opacity', 1);

    const result = this.validator.validate(cell);
    if (!result.valid && this.dragStartPosition) {
      this.animateBack(cell, this.dragStartPosition);
    }

    this.currentCell = null;
    this.dragStartPosition = null;
  }

  animateBack(cell, targetPosition) {
    const currentPos = cell.getPosition();
    const duration = 300;
    const startTime = Date.now();

    const animate = () => {
      const elapsed = Date.now() - startTime;
      const progress = Math.min(elapsed / duration, 1);
      const easeProgress = this.easeOutBack(progress);

      const newX = currentPos.x + (targetPosition.x - currentPos.x) * easeProgress;
      const newY = currentPos.y + (targetPosition.y - currentPos.y) * easeProgress;

      cell.setPosition(newX, newY, { silent: true });

      if (progress < 1) {
        requestAnimationFrame(animate);
      }
    };

    requestAnimationFrame(animate);
  }

  easeOutBack(t) {
    const c1 = 1.70158;
    const c3 = c1 + 1;
    return 1 + c3 * Math.pow(t - 1, 3) + c1 * Math.pow(t - 1, 2);
  }
}

/* ---------------- 8.  ---------------- */
class DropValidator {
  constructor(graph, validator) {
    this.graph = graph;
    this.validator = validator;
  }

  validateDrop(cell) {
    if (cell.shape !== 'sticky-note') return;

    this.validator.cacheMindMapBounds();
    const result = this.validator.validate(cell);

    if (!result.valid) {
      this.graph.removeCell(cell);
      this.showInvalidDropFeedback(result.reason);
    }
  }

  showInvalidDropFeedback(reason) {
    const message = reason === 'overlap' ? '' : '';
    showToast(message);
  }
}

/* ---------------- 9.  ---------------- */
class EditManager {
  constructor(graph) {
    this.graph = graph;
    this.isEditing = false;
    this.currentCell = null;
    this.input = null;
    this.isFinishing = false; // 
  }

  startEdit(cell) {
    if (!cell || cell.shape !== 'sticky-note' || this.isEditing) return;
    if (!this.graph.getCellById(cell.id)) return;

    this.isEditing = true;
    this.isFinishing = false;
    this.currentCell = cell;

    const currentText = cell.attr('label/text') || '';
    const placeholder = currentText === '' ? '' : currentText;

    const bbox = cell.getBBox();
    const graphContainer = this.graph.container;
    const containerRect = graphContainer.getBoundingClientRect();
    const scale = this.graph.zoom();
    const translate = this.graph.translate();

    const screenX = containerRect.left + bbox.x * scale + translate.tx;
    const screenY = containerRect.top + bbox.y * scale + translate.ty;
    const screenWidth = bbox.width * scale;
    const screenHeight = bbox.height * scale;

    // 
    cell.attr('label/opacity', 0);

    // 
    this.input = document.createElement('textarea');
    this.input.value = placeholder;
    this.input.placeholder = '...';
    Object.assign(this.input.style, {
      position: 'fixed',
      left: `${screenX}px`,
      top: `${screenY}px`,
      width: `${screenWidth}px`,
      height: `${screenHeight}px`,
      padding: '16px',
      background: '#FFF9C4',
      border: '2px solid #FFD54F',
      borderRadius: '10px',
      fontSize: `${14 * scale}px`,
      fontFamily: 'inherit',
      color: '#333',
      resize: 'none',
      outline: 'none',
      boxSizing: 'border-box',
      zIndex: '10000',
      textAlign: 'center',
      lineHeight: '1.4',
    });

    document.body.appendChild(this.input);

    // 
    setTimeout(() => {
      if (this.input) {
        this.input.focus();
        this.input.select();
      }
    }, 50);

    // 
    this.input.addEventListener('keydown', this.handleKeydown);

    // 
    this.input.addEventListener('touchstart', this.stopPropagation);
    this.input.addEventListener('touchmove', this.stopPropagation);
    this.input.addEventListener('touchend', this.stopPropagation);
    this.input.addEventListener('mousedown', this.stopPropagation);
  }

  stopPropagation = (e) => {
    e.stopPropagation();
  };

  handleKeydown = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      this.finishEdit(true);
    }
    if (e.key === 'Escape') {
      e.preventDefault();
      this.finishEdit(false);
    }
  };

  finishEdit(save) {
    // 
    if (!this.isEditing || this.isFinishing) return;
    this.isFinishing = true;

    const cell = this.currentCell;
    const inputValue = this.input ? this.input.value : '';

    if (save && cell && this.graph.getCellById(cell.id)) {
      const newText = inputValue.trim();
      if (newText) {
        cell.attr('label/text', newText);
        const { width, height } = calcStickySize(newText);
        cell.resize(
          Math.max(CANVAS_STICKY_SIZE.width, width),
          Math.max(CANVAS_STICKY_SIZE.height, height)
        );
      } else {
        cell.attr('label/text', '');
      }
    }

    // 
    if (cell && this.graph.getCellById(cell.id)) {
      cell.attr('label/opacity', 1);
    }

    this.cleanup();
  }

  cleanup() {
    if (this.input) {
      this.input.removeEventListener('keydown', this.handleKeydown);
      this.input.removeEventListener('touchstart', this.stopPropagation);
      this.input.removeEventListener('touchmove', this.stopPropagation);
      this.input.removeEventListener('touchend', this.stopPropagation);
      this.input.removeEventListener('mousedown', this.stopPropagation);
      if (document.body.contains(this.input)) {
        document.body.removeChild(this.input);
      }
      this.input = null;
    }

    this.isEditing = false;
    this.isFinishing = false;
    this.currentCell = null;
  }

  destroy() {
    this.finishEdit(false);
  }
}

/* ---------------- 10.  ---------------- */
class TouchInteractionManager {
  constructor(graph, editManager, dragManager) {
    this.graph = graph;
    this.editManager = editManager;
    this.dragManager = dragManager;

    this.touchStartTime = 0;
    this.touchStartPosition = null;
    this.currentCell = null;
    this.hasMoved = false;

    this.longPressTimer = null;
    this.isLongPress = false;
    this.deleteOverlay = null;
    this.dragMirror = null;
    this.touchOffset = { x: 0, y: 0 };
  }

  handleTouchStart(cell, e) {
    if (cell.shape !== 'sticky-note') return;

    // 
    if (this.editManager.isEditing) return;

    const touch = e.touches ? e.touches[0] : e;

    this.currentCell = cell;
    this.touchStartTime = Date.now();
    this.touchStartPosition = { x: touch.clientX, y: touch.clientY };
    this.hasMoved = false;
    this.isLongPress = false;

    this.longPressTimer = setTimeout(() => {
      if (!this.hasMoved) {
        this.isLongPress = true;
        this.enterDeleteMode(cell, e);
      }
    }, LONG_PRESS_DURATION);

    this.dragManager.startDrag(cell);
  }

  handleTouchMove(e) {
    if (!this.currentCell || !this.touchStartPosition) return;

    const touch = e.touches ? e.touches[0] : e;
    const currentX = touch.clientX;
    const currentY = touch.clientY;

    const distance = Math.sqrt(
      Math.pow(currentX - this.touchStartPosition.x, 2) +
        Math.pow(currentY - this.touchStartPosition.y, 2)
    );

    if (distance > MOVE_THRESHOLD) {
      this.hasMoved = true;
      if (!this.isLongPress) {
        this.cancelLongPress();
      }
    }

    if (this.isLongPress && this.dragMirror) {
      this.updateMirrorPosition(currentX, currentY);
      this.checkDeleteZone(currentY);
    }
  }

  handleTouchEnd(e) {
    if (!this.currentCell) return;

    const cell = this.currentCell;
    const touchDuration = Date.now() - this.touchStartTime;

    this.cancelLongPress();

    if (this.isLongPress) {
      this.handleLongPressEnd(e);
    } else if (touchDuration < TAP_THRESHOLD && !this.hasMoved) {
      //  - 
      this.dragManager.endDrag(cell);
      setTimeout(() => {
        this.editManager.startEdit(cell);
      }, 50);
    } else {
      // 
      this.dragManager.endDrag(cell);
    }

    this.resetState();
  }

  cancelLongPress() {
    if (this.longPressTimer) {
      clearTimeout(this.longPressTimer);
      this.longPressTimer = null;
    }
  }

  enterDeleteMode(cell, e) {
    cell.attr('body/opacity', 0.3);
    this.createDragMirror(cell, e);
    this.createDeleteOverlay();

    if (navigator.vibrate) {
      navigator.vibrate(50);
    }
  }

  createDragMirror(cell, e) {
    const bbox = cell.getBBox();
    const graphContainer = this.graph.container;
    const containerRect = graphContainer.getBoundingClientRect();
    const scale = this.graph.zoom();
    const translate = this.graph.translate();

    const screenX = containerRect.left + bbox.x * scale + translate.tx;
    const screenY = containerRect.top + bbox.y * scale + translate.ty;
    const screenWidth = bbox.width * scale;
    const screenHeight = bbox.height * scale;

    const touch = e.touches ? e.touches[0] : e;
    this.touchOffset = {
      x: touch.clientX - (screenX + screenWidth / 2),
      y: touch.clientY - (screenY + screenHeight / 2),
    };

    const text = cell.attr('label/text') || '';

    this.dragMirror = document.createElement('div');
    Object.assign(this.dragMirror.style, {
      position: 'fixed',
      left: `${screenX}px`,
      top: `${screenY}px`,
      width: `${screenWidth}px`,
      height: `${screenHeight}px`,
      background: '#FFF9C4',
      borderRadius: '16px',
      boxShadow: '0 8px 24px rgba(0, 0, 0, 0.25)',
      border: '1px solid #ff4444',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      padding: '12px',
      boxSizing: 'border-box',
      fontSize: `${14 * scale}px`,
      color: '#333',
      textAlign: 'center',
      whiteSpace: 'pre-wrap',
      wordBreak: 'break-word',
      zIndex: '10000',
      pointerEvents: 'none',
      transform: 'scale(1.05)',
      transition: 'transform 0.1s, box-shadow 0.1s',
    });

    this.dragMirror.textContent = text;
    document.body.appendChild(this.dragMirror);
  }

  updateMirrorPosition(x, y) {
    if (!this.dragMirror) return;

    const mirrorWidth = this.dragMirror.offsetWidth;
    const mirrorHeight = this.dragMirror.offsetHeight;
    const mirrorX = x - mirrorWidth / 2 - this.touchOffset.x;
    const mirrorY = y - mirrorHeight / 2 - this.touchOffset.y;

    this.dragMirror.style.left = `${mirrorX}px`;
    this.dragMirror.style.top = `${mirrorY}px`;

    const isInDeleteZone = y > window.innerHeight - 120;

    if (isInDeleteZone) {
      this.dragMirror.style.transform = 'scale(0.9)';
      this.dragMirror.style.opacity = '0.7';
      this.dragMirror.style.borderColor = '#ff0000';
    } else {
      this.dragMirror.style.transform = 'scale(1.05)';
      this.dragMirror.style.opacity = '1';
      this.dragMirror.style.borderColor = '#ff4444';
    }
  }

  createDeleteOverlay() {
    this.deleteOverlay = document.createElement('div');
    Object.assign(this.deleteOverlay.style, {
      position: 'fixed',
      bottom: '0',
      left: '0',
      right: '0',
      height: '120px',
      background: 'linear-gradient(to bottom, rgba(255,68,68,0) 0%, rgba(255,68,68,0.3) 100%)',
      display: 'flex',
      alignItems: 'flex-end',
      justifyContent: 'center',
      paddingBottom: '20px',
      zIndex: '9999',
      transition: 'background 0.2s',
      pointerEvents: 'none',
    });

    const deleteIcon = document.createElement('div');
    Object.assign(deleteIcon.style, {
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      gap: '8px',
    });

    deleteIcon.innerHTML = `
      <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="#ff4444" stroke-width="2">
        <polyline points="3 6 5 6 21 6"></polyline>
        <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
        <line x1="10" y1="11" x2="10" y2="17"></line>
        <line x1="14" y1="11" x2="14" y2="17"></line>
      </svg>
      <div class="delete-text" style="color: #ff4444; font-size: 14px; font-weight: bold;"></div>
    `;

    this.deleteOverlay.appendChild(deleteIcon);
    document.body.appendChild(this.deleteOverlay);
  }

  checkDeleteZone(y) {
    const isInDeleteZone = y > window.innerHeight - 120;

    if (this.deleteOverlay) {
      const deleteText = this.deleteOverlay.querySelector('.delete-text');
      const svg = this.deleteOverlay.querySelector('svg');

      if (isInDeleteZone) {
        this.deleteOverlay.style.background =
          'linear-gradient(to bottom, rgba(255,68,68,0) 0%, rgba(255,0,0,0.5) 100%)';
        if (deleteText) {
          deleteText.textContent = '';
          deleteText.style.color = '#ff0000';
        }
        if (svg) {
          svg.style.stroke = '#ff0000';
          svg.style.transform = 'scale(1.2)';
          svg.style.transition = 'transform 0.2s';
        }
      } else {
        this.deleteOverlay.style.background =
          'linear-gradient(to bottom, rgba(255,68,68,0) 0%, rgba(255,68,68,0.3) 100%)';
        if (deleteText) {
          deleteText.textContent = '';
          deleteText.style.color = '#ff4444';
        }
        if (svg) {
          svg.style.stroke = '#ff4444';
          svg.style.transform = 'scale(1)';
        }
      }
    }

    return isInDeleteZone;
  }

  handleLongPressEnd(e) {
    const cell = this.currentCell;

    if (cell && this.graph.getCellById(cell.id)) {
      cell.attr('body/opacity', 1);
    }

    let endY;
    if (e.changedTouches) {
      endY = e.changedTouches[0].clientY;
    } else if (e.touches) {
      endY = e.touches[0].clientY;
    } else {
      endY = e.clientY;
    }

    const isInDeleteZone = endY > window.innerHeight - 120;

    if (isInDeleteZone) {
      this.playDeleteAnimation(() => {
        if (cell && this.graph.getCellById(cell.id)) {
          this.graph.removeCell(cell);
          showToast('');
        }
      });
    } else {
      this.playRestoreAnimation();
    }

    this.cleanupDeleteMode();
  }

  playDeleteAnimation(callback) {
    if (!this.dragMirror) {
      callback();
      return;
    }

    this.dragMirror.style.transition = 'all 0.3s ease-in';
    this.dragMirror.style.transform = 'scale(0)';
    this.dragMirror.style.opacity = '0';

    setTimeout(() => {
      callback();
      this.removeMirror();
    }, 300);
  }

  playRestoreAnimation() {
    if (!this.dragMirror || !this.currentCell) {
      this.removeMirror();
      return;
    }

    const bbox = this.currentCell.getBBox();
    const graphContainer = this.graph.container;
    const containerRect = graphContainer.getBoundingClientRect();
    const scale = this.graph.zoom();
    const translate = this.graph.translate();

    const targetX = containerRect.left + bbox.x * scale + translate.tx;
    const targetY = containerRect.top + bbox.y * scale + translate.ty;

    this.dragMirror.style.transition = 'all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275)';
    this.dragMirror.style.left = `${targetX}px`;
    this.dragMirror.style.top = `${targetY}px`;
    this.dragMirror.style.transform = 'scale(1)';
    this.dragMirror.style.opacity = '1';
    this.dragMirror.style.border = 'none';

    setTimeout(() => {
      this.removeMirror();
    }, 300);
  }

  removeMirror() {
    if (this.dragMirror && document.body.contains(this.dragMirror)) {
      document.body.removeChild(this.dragMirror);
      this.dragMirror = null;
    }
  }

  cleanupDeleteMode() {
    if (this.deleteOverlay && document.body.contains(this.deleteOverlay)) {
      document.body.removeChild(this.deleteOverlay);
      this.deleteOverlay = null;
    }
  }

  resetState() {
    this.currentCell = null;
    this.touchStartPosition = null;
    this.hasMoved = false;
    this.isLongPress = false;
  }

  destroy() {
    this.cancelLongPress();
    this.removeMirror();
    this.cleanupDeleteMode();
  }
}

/* ---------------- 11.  ---------------- */
class GestureManager {
  constructor(graph, container, touchInteractionManager, editManager) {
    this.graph = graph;
    this.container = container;
    this.touchInteractionManager = touchInteractionManager;
    this.editManager = editManager;
    this.touchStartPoint = null;
    this.initialPinchDistance = null;
    this.lastMoveTime = 0;
    this.isPanning = false;

    this.onTouchStart = this.onTouchStart.bind(this);
    this.onTouchMove = this.onTouchMove.bind(this);
    this.onTouchEnd = this.onTouchEnd.bind(this);

    this.setupEventListeners();
  }

  setupEventListeners() {
    this.container.addEventListener('touchstart', this.onTouchStart, { passive: false });
    this.container.addEventListener('touchmove', this.onTouchMove, { passive: false });
    this.container.addEventListener('touchend', this.onTouchEnd, { passive: false });
  }

  onTouchStart(e) {
    // 
    if (this.editManager.isEditing) {
      // 
      const target = e.target;
      if (target !== this.editManager.input) {
        e.preventDefault();
        this.editManager.finishEdit(true);
        return;
      }
    }

    if (e.touches.length === 1) {
      this.touchStartPoint = {
        x: e.touches[0].clientX,
        y: e.touches[0].clientY,
      };
      this.isPanning = false;
    } else if (e.touches.length === 2) {
      e.preventDefault();
      this.initialPinchDistance = Math.hypot(
        e.touches[1].clientX - e.touches[0].clientX,
        e.touches[1].clientY - e.touches[0].clientY
      );
    }
  }

  onTouchMove(e) {
    // 
    if (this.editManager.isEditing) {
      return;
    }

    // 
    if (this.touchInteractionManager) {
      this.touchInteractionManager.handleTouchMove(e);
    }

    // 
    if (this.touchInteractionManager && this.touchInteractionManager.isLongPress) {
      return;
    }

    // 
    if (
      this.touchInteractionManager &&
      this.touchInteractionManager.currentCell &&
      this.touchInteractionManager.hasMoved
    ) {
      return;
    }

    const now = Date.now();
    if (now - this.lastMoveTime < DEBOUNCE_DELAY) return;
    this.lastMoveTime = now;

    if (e.touches.length === 1 && this.touchStartPoint) {
      const touch = e.touches[0];
      const dx = touch.clientX - this.touchStartPoint.x;
      const dy = touch.clientY - this.touchStartPoint.y;

      if (!this.isPanning && (Math.abs(dx) > 5 || Math.abs(dy) > 5)) {
        this.isPanning = true;
      }

      if (this.isPanning) {
        this.graph.translateBy(dx, dy);
        this.touchStartPoint = { x: touch.clientX, y: touch.clientY };
      }
    } else if (e.touches.length === 2 && this.initialPinchDistance) {
      e.preventDefault();
      const currentDistance = Math.hypot(
        e.touches[1].clientX - e.touches[0].clientX,
        e.touches[1].clientY - e.touches[0].clientY
      );

      const scaleChange = currentDistance / this.initialPinchDistance;
      const currentScale = this.graph.zoom();
      const newScale = Math.max(0.5, Math.min(3, currentScale * scaleChange));

      const centerX = (e.touches[0].clientX + e.touches[1].clientX) / 2;
      const centerY = (e.touches[0].clientY + e.touches[1].clientY) / 2;

      this.graph.zoomTo(newScale, { x: centerX, y: centerY });
      this.initialPinchDistance = currentDistance;
    }
  }

  onTouchEnd() {
    this.touchStartPoint = null;
    this.initialPinchDistance = null;
    this.isPanning = false;
  }

  destroy() {
    this.container.removeEventListener('touchstart', this.onTouchStart);
    this.container.removeEventListener('touchmove', this.onTouchMove);
    this.container.removeEventListener('touchend', this.onTouchEnd);
  }
}

/* ---------------- 12. Stencil  ---------------- */
class StencilStyleManager {
  constructor(stencilContainer) {
    this.container = stencilContainer;
    this.observer = null;
  }

  applyStyles() {
    try {
      const titles = this.container.querySelectorAll('.x6-widget-stencil-title');
      titles.forEach((title) => (title.style.display = 'none'));

      const stencilElement = this.container.querySelector('.x6-widget-stencil');
      if (stencilElement) {
        Object.assign(stencilElement.style, {
          overflow: 'visible',
          background: 'transparent',
          border: 'none',
        });
      }

      const stencilContent = this.container.querySelector('.x6-widget-stencil-content');
      if (stencilContent) {
        Object.assign(stencilContent.style, {
          overflow: 'visible',
          background: 'transparent',
          border: 'none',
          minHeight: '120px',
        });
      }

      const stencilGraph = this.container.querySelector('.x6-widget-stencil-graph');
      if (stencilGraph) {
        Object.assign(stencilGraph.style, {
          overflow: 'visible',
          background: 'transparent',
          border: 'none',
        });
      }

      const nodes = this.container.querySelectorAll('.x6-node');
      nodes.forEach((node) => {
        Object.assign(node.style, {
          overflow: 'visible',
          background: 'transparent',
          border: 'none',
          boxShadow: 'none',
        });

        const rect = node.querySelector('rect');
        if (rect) {
          rect.style.fill = '#FFF9C4';
          rect.style.stroke = 'none';
          rect.style.cursor = 'grab';
          rect.style.filter = 'drop-shadow(4px 6px 12px rgba(0,0,0,0.15))';
          rect.setAttribute('rx', '16');
          rect.setAttribute('ry', '16');
        }

        const text = node.querySelector('text');
        if (text) {
          text.style.fill = '#333';
          text.style.fontSize = '14px';
          text.style.textAnchor = 'middle';
        }
      });
    } catch (error) {
      console.warn('Stencil :', error);
    }
  }

  startObserving() {
    this.observer = new MutationObserver((mutations) => {
      const hasNodeChanges = mutations.some((mutation) =>
        Array.from(mutation.addedNodes).some(
          (node) => node.nodeType === 1 && node.classList?.contains('x6-node')
        )
      );
      if (hasNodeChanges) {
        this.applyStyles();
      }
    });

    this.observer.observe(this.container, { childList: true, subtree: true });
  }

  destroy() {
    if (this.observer) {
      this.observer.disconnect();
    }
  }
}

/* ---------------- 13.  ---------------- */
export default function MindMap() {
  const containerRef = useRef(null);
  const stencilContainerRef = useRef(null);
  const graphRef = useRef(null);
  const managersRef = useRef({});

  useEffect(() => {
    if (!containerRef.current || !stencilContainerRef.current) return;

    const hierarchyData = convertToHierarchyData(treeData.nodes);
    if (!hierarchyData) return;

    const availableWidth = window.innerWidth - STENCIL_WIDTH;

    const graph = new Graph({
      container: containerRef.current,
      width: availableWidth,
      height: window.innerHeight,
      background: { color: 'rgba(255,255,255,0.1)' },
      interacting: {
        nodeMovable: (cell) => cell.shape === 'sticky-note',
      },
      selecting: { enabled: false },
      panning: { enabled: false },
      mousewheel: {
        enabled: true,
        modifiers: [],
        factor: 1.05,
        minScale: 0.5,
        maxScale: 3,
      },
      autoResize: true,
      grid: { visible: false },
      scaling: { min: 0.5, max: 3 },
    });

    graphRef.current = graph;

    const validator = new NodeValidator(graph);
    const dragManager = new DragManager(graph, validator);
    const dropValidator = new DropValidator(graph, validator);
    const editManager = new EditManager(graph);
    const touchInteractionManager = new TouchInteractionManager(graph, editManager, dragManager);
    //  editManager  GestureManager
    const gestureManager = new GestureManager(
      graph,
      containerRef.current,
      touchInteractionManager,
      editManager
    );

    managersRef.current = {
      validator,
      dragManager,
      dropValidator,
      editManager,
      touchInteractionManager,
      gestureManager,
    };

    const hierarchy = Hierarchy.mindmap(hierarchyData, {
      direction: 'H',
      getHeight: (d) => d.height,
      getWidth: (d) => d.width,
      getHGap: () => 18,
      getVGap: () => 18,
      getSide: () => 'right',
    });

    const cells = [];
    const traverse = (item) => {
      const { data, children, x, y } = item;
      const node = graph.createNode({
        id: data.id,
        shape: data.type,
        x,
        y,
        width: data.width,
        height: data.height,
        label: data.label,
      });
      node.setAttrs({ body: { fill: data.color, stroke: data.color } });
      cells.push(node);

      children?.forEach((child) => {
        cells.push(
          graph.createEdge({
            shape: 'smooth-edge',
            source: { cell: data.id, anchor: { name: 'right' } },
            target: { cell: child.id, anchor: { name: 'left' } },
          })
        );
        traverse(child);
      });
    };
    traverse(hierarchy);
    graph.resetCells(cells);

    // 
    initialStickyNotes.forEach((stickyData) => {
      const { width, height } = calcStickySize(stickyData.text);
      const stickyNode = graph.createNode({
        id: stickyData.id,
        shape: 'sticky-note',
        x: stickyData.x,
        y: stickyData.y,
        width: Math.max(CANVAS_STICKY_SIZE.width, width),
        height: Math.max(CANVAS_STICKY_SIZE.height, height),
        attrs: {
          label: { text: stickyData.text },
          body: { fill: '#FFF9C4' },
        },
      });
      graph.addNode(stickyNode);
    });

    graph.scale(0.6);
    const contentBounds = graph.getContentBBox();
    const graphCenterX = availableWidth / 2;
    const graphCenterY = window.innerHeight / 2;
    const targetX = graphCenterX - (contentBounds.x + contentBounds.width / 2) * 0.6;
    const targetY = graphCenterY - (contentBounds.y + contentBounds.height / 2) * 0.6;
    graph.translate(targetX-60, targetY+50);

    const stencil = new Stencil({
      target: graph,
      stencilGraphWidth: 120,
      stencilGraphHeight: 120,
      groups: [{ name: 'group1', title: '', collapsable: false }],
      layout: (model) => {
        model.getCells().forEach((cell) => cell.setPosition(20, 20));
      },
      getDragNode: (node) =>
        node.clone().resize(STENCIL_STICKY_SIZE.width, STENCIL_STICKY_SIZE.height),
      getDropNode: (node) => {
        const cloned = node.clone();
        cloned.resize(CANVAS_STICKY_SIZE.width, CANVAS_STICKY_SIZE.height);
        cloned.attr('label/text', '');
        return cloned;
      },
      validateNode: (droppingNode) => {
        validator.cacheMindMapBounds();
        const pos = droppingNode.getPosition();
        const size = droppingNode.getSize();
        const result = validator.validatePosition(pos, size);

        if (!result.valid) {
          dropValidator.showInvalidDropFeedback(result.reason);
          return false;
        }
        return true;
      },
    });

    stencilContainerRef.current.appendChild(stencil.container);

    const stickyTemplate = graph.createNode({
      shape: 'sticky-note',
      width: STENCIL_STICKY_SIZE.width,
      height: STENCIL_STICKY_SIZE.height,
      position: { x: 20, y: 20 },
      attrs: {
        label: { text: '' },
        body: { fill: '#FFF9C4' },
      },
    });
    stencil.load([stickyTemplate], 'group1');

    const stencilStyleManager = new StencilStyleManager(stencilContainerRef.current);
    setTimeout(() => {
      stencilStyleManager.applyStyles();
      stencilStyleManager.startObserving();
    }, 100);

    // 
    graph.on('node:mousedown', ({ cell, e }) => {
      if (cell.shape === 'sticky-note') {
        touchInteractionManager.handleTouchStart(cell, e.originalEvent);
      }
    });

    graph.on('node:mouseup', ({ cell, e }) => {
      if (cell.shape === 'sticky-note') {
        touchInteractionManager.handleTouchEnd(e.originalEvent);
      }
    });

    // 
    graph.on('node:added', ({ cell }) => {
      if (cell.shape === 'sticky-note') {
        cell.setProp('movable', true);
        setTimeout(() => dropValidator.validateDrop(cell), 50);
      }
    });

    const handleResize = debounce(() => {
      const newAvailableWidth = window.innerWidth - STENCIL_WIDTH;
      graph.resize(newAvailableWidth, window.innerHeight);
    }, 100);

    window.addEventListener('resize', handleResize);

    return () => {
      editManager.destroy();
      graph.dispose();
      stencil.dispose();
      stencilStyleManager.destroy();
      gestureManager.destroy();
      touchInteractionManager.destroy();
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <div
      style={{
        width: '100vw',
        height: '100vh',
        overflow: 'hidden',
        display: 'flex',
        background: '#f5f5f5',
      }}
    >
      <div
        style={{
          width: `${STENCIL_WIDTH}px`,
          height: '100%',
          background: 'white',
          boxSizing: 'border-box',
          display: 'flex',
          flexDirection: 'column',
          borderRight: '1px solid rgba(225,225,225,0.8)',
          overflow: 'hidden',
        }}
      >
        <div
          ref={stencilContainerRef}
          style={{ flex: 1, overflow: 'hidden', position: 'relative' }}
        />
      </div>

      <div
        style={{
          flex: 1,
          height: '100%',
          background: 'white',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <div ref={containerRef} style={{ width: '100%', height: '100%' }} />
      </div>
    </div>
  );
}